import { onAuthStateChanged, signOut } from "firebase/auth";
import { auth } from "../firebaseConfig.js";

export function TopBar(props) {
  function handleBackToSearchACB() {
    window.location.hash = "#/search";
  }

  function handleBackToUserACB() {
    window.location.hash = "#/user";
  }

  function handleBackToCollectionsACB() {
    window.location.hash = "#/collections";
  }

  async function handleLogout() {
    try {
      await signOut(auth);
    } catch (error) {
      console.error("Logout error:", error);
    }
    window.location.hash = "#/";
  }

  return (
    <div>
      <header className="app-header">
        <h1> LeafKeeper</h1>
      </header>
      <button>Menu</button>
      <button onClick={handleBackToSearchACB}>Discover</button>
      <button onClick={handleBackToCollectionsACB}>My plant collections</button>
      <button onClick={handleBackToUserACB}>User</button>
      <button onClick={handleLogout} className="logout-btn">
        Logout
      </button>
    </div>
  );
}
