import { useState } from "react";
import "./loginSignupStyle.css";

// Open eye = password is VISIBLE (click to hide)
function EyeIcon() {
  return (
    <svg viewBox="0 0 24 24">
      <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z" />
      <circle cx="12" cy="12" r="3" />
    </svg>
  );
}

// Crossed eye = password is HIDDEN (click to show)
function EyeOffIcon() {
  return (
    <svg viewBox="0 0 24 24">
      <path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94" />
      <path d="M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19" />
      <path d="M14.12 12.12a3 3 0 1 1-4.24-4.24" />
      <line x1="1" y1="1" x2="23" y2="23" />
    </svg>
  );
}


export function LoginView(props) {
  const [showPassword, setShowPassword] = useState(false);

  function handleSubmit(event) {
    event.preventDefault();
    props.onLogin();
  }

  return (
    <>
      <div className="auth-container">
        <h2> LeafKeeper</h2>
        <p className="auth-subtitle">Welcome back to your garden</p>
        
        {props.error && (<div className="error-message">
          {props.error}
        </div>)}
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              placeholder="your@email.com"
              value={props.email}
              onChange={(e) => props.onEmailChange(e.target.value)}
              required
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="password">Password</label>
            <div className="password-wrapper">
              <input
                type={showPassword ? "text" : "password"}
                id="password"
                placeholder=""
                value={props.password}
                onChange={(e) => props.onPasswordChange(e.target.value)}
                required
              />
              <button
                type="button"
                className="password-toggle"
                onClick={() => setShowPassword(!showPassword)}
                aria-label={showPassword ? "Hide password" : "Show password"}
              >
                {showPassword ? <EyeIcon /> : <EyeOffIcon />}
              </button>
            </div>
          </div>
          
          <button type="submit" className="submit-btn" disabled={props.loading}>
            {props.loading ? "Signing in..." : "Sign In"}
          </button>
        </form>
        
        <p className="switch-link">
          New to LeafKeeper?{" "}
          <a onClick={props.onSwitchToSignup}>Create an account</a>
        </p>
      </div>
    </>
  );
}